.App {
  text-align: center;
  height: 100%;
  position: relative;
  padding: 0rem 2rem 0px 2rem;
}

.Background {
  background: url('./../img/background.jpg');
  background-size: cover;
  position: fixed;
  top: 0; 
  left: 0; 
  z-index: -2;
  min-width: 100%;
  min-height: 100%;
}

.Opacity{
  background: black;
  opacity: 0.5;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0; 
  left: 0;
  right: 0;
  z-index: -1;
}

.Header{
  display: -ms-flexbox;
  display: flex;
  color: #50fdec;
  padding: 2rem 0px 0px 0px;
}

.Header-Left{
  -ms-flex: 3 1;
      flex: 3 1;
  text-align: left;
}

.Header-Right{
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: right;
}

.Logo-Marrow{
  height: 2.5vw;
  max-height: 3rem;
  margin: 0px 2vw 0px 0px;
}

.Logo-Idfa{
  height: 4.5rem;
}

.Header-Left-Top{
  display: -ms-flexbox;
  display: flex;
}

.Header-Left-Top ul{
  display: inline;
}

.Header-Left-Top li{
  margin: 0px 20px; 
}

.Header-Left-Description{
  font-weight: 300;
}

.Main-Title h2{
  color: white;
  font-size: 3.8rem;
  text-align: right;
  font-weight: 700;
  margin: 0px;
  padding: 0px;
  line-height: 3.5rem;
}

.Main-Description{

}

.Main-Description h3{
  text-align: right;
  color: white;
  font-weight: 500;
  font-size: 1rem;
  line-height: 0.3rem;
}

.Footer{
  opacity: 0.8;
  margin: 3rem 0px 0px 0px;
  /* position: absolute;
  bottom: 1rem;
  left: 0; 
  right: 0; 
  margin-left: auto; 
  margin-right: auto; 
  padding: 0px 1rem; */
}

@media only screen and (max-width: 600px) {
  .Footer {
    position: inherit;
  }
}


.Footer-Title h4{
  color: white;
  letter-spacing: -0.2px;
  font-weight: 500;
}

.Footer-Logos{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 1rem 0px 0px 0px;
}

.Footer-Logos img{
  width: 50px;
  height: 100%;
  margin: 0px 1rem;
}

.Footer-Logos img:hover{
  cursor: pointer;
}

.Footer-Logos a:hover{
  border: none !important;
}

.Footer-Logo-Large{
  margin: 0px 1rem;
}


